@media screen and (max-width: 768px) {
  .animalia-top-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* height: 40vh; */
  }

  .big-five {
    width: 768px;
  }

  .top-description {
    font-size: 44px;
    color: #fff;
    text-align: center;
  }

  .animalia-dropdown {
    display: flex;
    flex-direction: row;
    padding: 24px;
  }

  .animal-images {
    width: 12rem;
    height: 10rem;
    border-radius: 8px;
  }

  .animalia-description {
    display: grid;
    justify-content: end;
    padding-right: 4rem;
  }

  .animalia-description h2 {
    font-size: 1.3rem;
  }

  .animalia-description h3 {
    font-size: 1.2rem;
  }

  .animal-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    align-items: center;
    column-gap: 30px;
    row-gap: 30px;
    justify-content: center;
    margin-left: 12%;
  }

  .animalia-info-div {
    max-width: 38rem;
    color: #fff;
    text-align: center;
    padding: 50px 35px;
    border: 1px solid rgba(255,255,255,0.3);
    background: rgba(255,255,255,0.2);
    border-radius: 18px;
    box-shadow: 10px 5px 5px rgba(227, 226, 226, 0.634);
    backdrop-filter: blur(5px);
  }

  .arrow-round {
    width: 1.8rem;
    margin-left: 24px;
  }
}

@media screen and (min-width: 768px) {
  .animalia-top-section {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-size: 100% 100%;
    justify-content: space-around;
    align-items: center;
    width: 100%;
  }

  .big-five {
    width: 39rem;
  }

  .top-description {
    font-size: 44px;
    color: #fff;
  }

  .animalia-dropdown {
    display: flex;
    flex-direction: row;
    padding: 24px;
  }

  .animal-images {
    width: 28rem;
    height: 21rem;
    border-radius: 8px;
  }

  .animalia-description {
    display: grid;
    justify-content: end;
    padding-right: 4rem;
  }

  .animal-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    align-items: center;
    column-gap: 50px;
    row-gap: 50px;
    justify-content: center;
    margin: auto;
    margin: 0 16rem 0 16rem;
  }

  .animalia-info-div {
    max-width: 38rem;
    color: #fff;
    text-align: center;
    padding: 50px 35px;
    border: 1px solid rgba(255,255,255,0.3);
    background: rgba(255,255,255,0.2);
    border-radius: 18px;
    box-shadow: 10px 5px 5px rgba(227, 226, 226, 0.634);
    backdrop-filter: blur(5px);
  }

  .arrow-round {
    width: 1.8rem;
    margin-left: 24px;
  }
}
